.todoItem{
    background-color: var(--tertiary-color);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 15px;
}
.todoItem-p{
    margin:24px 0 24px 24px;
    width: calc(100%-120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}
.todoItem-p--complete{
    text-decoration: line-through;
}
.icon{
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 34px;
    font-weight: bold;
}
.icon-check{
    position: absolute;
    left: 12px;
}
.icon-check--active{
    color: var(--quaternary-color);
}
.icon-delete{
    position: absolute;
    top: -26px;
    right: 0;
}
.icon-delete:hover{
    color: red;
}